@import "~antd/dist/antd.css";
input {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}
.login-page {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #a7a8a7;
}
.login-page > h1 {
  text-align: center;
}
.login-page > h1 > sup {
  color: #3d1116;
}
.login-button {
  margin-top: 1rem;
  cursor: pointer;
  padding: 5px 10px;
  border: 2px solid black;
  border-radius: 5px;
}
.login-button > h3 {
  color: #3d1116;
}
.login-button:hover {
  transform: scale(1.1);
}
